<template lang="html">
        <div class="_full_router ">
            	<custHeader :title="title"></custHeader>
            <div style="background:#fff;">
                <div style="position:relative;"><canvas ref="cvs"></canvas><div class="vip-v"><p>95<br/><span>v1</span></p></div></div>
                <div class="normal-box ponits-show">
                    <div class="ponits-y"><p>25</p><p class="t-t">市场制度遵守</p></div>
                    <div class="ponits-b"><p>25</p><p class="t-t">资产总资</p></div>
                    <div class="ponits-y2"><p>25</p><p class="t-t">净入金量</p></div>
                    <div class="ponits-b2"><p>25</p><p class="t-t">交割比率</p></div>
                </div>
            </div>
            <div style="background:#fff;margin-top:12px;">
                <p style="text-align:center;font-size:18px;margin-bottom:12px;">
                    积分值详细介绍
                </p>
                <section>
                    <em>分值与积分对照</em>
                    <p>
                        >0 and <=30 为v0<br/>
                        >30 and <=60 为v1<br/>
                        >60 and <=80 为v2<br/>
                        >80 and <=100 为v3<br/>
                    </p>
                </section>
                <section>
                    <em>市场制度遵守</em>
                    <p>
                        市场制度遵守情况满分25分
                    </p>
                </section>
                <section>
                    <em>资产总资</em>
                    <p>
                        >20000 and <=100000 为5分<br/>
                        >100000 and <=200000 为10分<br/>
                        >200000 and <=500000 为15分<br/>
                        >500000 and <=1000000 为20分<br/>
                        >1000000 and <=99999999999.99 为25分<br/>
                    </p>
                </section>
                <section>
                    <em>净入金量</em>
                    <p>
                        >20000 and <=100000 为5分<br/>
                        >100000 and <=200000 为10分<br/>
                        >200000 and <=500000 为15分<br/>
                        >500000 and <=1000000 为20分<br/>
                        >1000000 and <=99999999999.99 为25分<br/>
                    </p>
                </section>
                <section>
                    <em>商品交割比率</em>
                    <p>
                        >0.00 and <=50.00 为5分<br/>
                        >50.00 and <=60.00为10分<br/>
                        >200000 and <=500000 为15分<br/>
                        >500000 and <=1000000 为20分<br/>
                        >1000000 and <=99999999999.99 为25分<br/>
                    </p>
                </section>
            </div>
        </div>
</template>

<script>
import custHeader from  'components/custHeader'
export default {
    data(){
        return{
            title:"积分说明"
        }
    },
    created:function(){
        console.log(this.$refs.cvs)
        this.$nextTick(()=>{
            var ctx=this.$refs.cvs.getContext('2d');
            var nums=[25,25,25,25];
            var colors=["#FB8D1D", "#FBC602","#32C5F7","#6B7FF7"];
            var start=0;
            var end=0;
            console.log();
            this.$refs.cvs.style.width="100%";
            this.$refs.cvs.height=160;
            console.log(this.$refs.cvs.height);
            ctx.translate(150,80);
            function pieChart(){
                for(var i=0;i<nums.length;i++){
                    ctx.beginPath();
                    ctx.moveTo(0,0);
                    end+=nums[i]/50*Math.PI;//终止角度
                    ctx.strokeStyle="white";
                    ctx.fillStyle=colors[i];
                    ctx.arc(0,0,80,start,end);
                    ctx.fill();
                    ctx.closePath();
                    ctx.stroke();
                    start+=nums[i]/50*Math.PI;//下一个起始角度
                }
            }
            ctx.rotate(-Math.PI/2);
            pieChart();
        })



    },
components:{

    custHeader
}
}
</script>

<style lang="css" scoped>
._full_router{
    background: #eee;
}
._full_router>div{
    padding: 12px;
}
.vip-v{
    position: absolute;
top: 50%;
left: 50%;
width: 100px;
height: 100px;
background: #fff;
margin-left: -50px;
margin-top: -50px;
border-radius: 100px;
line-height: 100px;
text-align: center;
font-size: 28px;
}
.ponits-show{
    font-size:18px;
    text-align: center;
}
.ponits-show .t-t{
    font-size: 12px;
}
.ponits-y{
    color: #FB8D1D;
}
.ponits-y2{
    color: #FBC602;
}
.ponits-b{
    color: #32C5F7;
}
.ponits-b2{
    color: #6B7FF7;
}
section>em{
    font-size: 16px;
    font-weight: bold;
}
section>p{
    font-size: 12px;
    color: #666;
    padding: 12px 0;
}
</style>



